body {
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
        "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
    background-color: rgba(211, 211, 211, 0.174);
    color: rgb(0, 102, 0);
}

.picture {
    position: absolute;
    top: 30px;
    right: 120px;
    width: 120px;
    height: 120px;
    border: 2px solid navy;
    border-radius: 10px;
}

.main {
    display: flex;
    justify-items: center;
    border: 3px solid navy;
    justify-content: center;
    flex-direction: column;
}

.pinfo {
    text-align: center;
    list-style: none;
    margin: 0px;
}

.subhead-1 {
    margin-top: 45px;
    text-align: center;
}

.subhead-all {
    text-align: center;
}

.noborder {
    border: none;
}

span {
    font-size: larger;
    padding: 3px;
    border: 1px solid black;
    border-radius: 10px;
    margin-left: 10px;
    line-height: 50px;
}

.hovereffect:hover {
    background-color: navy;
    color: whitesmoke;
    border: 2px solid red;
    border-radius: 10px;
}

ul {
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 10px;
    list-style: none;
}


h1 {
    color: blue;
}

h2 {
    margin: 0px;
    margin-left: 100px;
    margin-right: 100px;
    background-color: midnightblue;
    color: whitesmoke;
}

h3 {
    margin: 0px;
    margin-left: 100px;
    margin-right: 100px;
    color: blue;
}

a {
    color: rgb(0, 102, 0);
}

.details {
    margin-top: 0px;
    margin-bottom: 0px;
    margin-left: 90px;
    margin-right: 7px;
}